.modal-overlay {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, .60);
  z-index: 3;
}

.modal-delete {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 175px;
  width: 350px;
  padding: 25px 83px;
  background: $light-font;
}

.modal-delete__title {
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 17px;
  color: black;
  margin-bottom: 20px;
}

.modal-delete__close-button {
  position: absolute;
  top: 12px;
  right: 12px;
  height: 24px;
  width: 24px;
  background: url(../img/svg/close-btn.svg) no-repeat center;
  border: 0;
}

.modal-delete__delete-button {
  // height: 50px;
  width: 162px;
  padding: 13px 20px;
  border: 0;
  margin-bottom: 15px;
}

.modal-delete__cancel-button {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 17px;
  text-decoration: underline;
  border: 0;
  background: transparent;
}